@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@700&family=Quicksand:wght@500&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@700&family=Quicksand:wght@500;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-gray-600;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
